import { Box } from "@mui/material";
import React from "react";
import { getHoursWorked, hoursPastInDay } from "../../functions/time";
import { myClasses } from "../../classes/style";

function DaySummary(props) {
  if (props.day.timeIn && props.day.timeOut) {
    const hoursWorked = getHoursWorked(props.day);
    return (
      <Box sx={{ ...myClasses.flexCol }}>
        {hoursWorked} {props.button}
      </Box>
    );
  } else {
    return <p>Day not done</p>;
  }
}

export default DaySummary;
