import { CheckBox, Label } from "@mui/icons-material";
import { Checkbox, Stack, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { objectStateUpdate } from "../../functions";
import { day } from "../../storage/example";
import TimeDisplay from "../inputs/TimeDisplay";
import TimePicker from "../inputs/TimePicker";
import Button from "../inputs/Button.js";
import DaySuimmary from "../displays/DaySummary";
import DatePicker from "../inputs/DatePicker";
import DateDisplay from "../inputs/DateDisplay";
import { myClasses, myColors } from "../../classes/style";
import { validateIn } from "../../classes/validation";
import DataLabel from "../text/DataLabel";
import { v4 as uuid } from "uuid";
let initialDay = day;
const cachedDay = lsCache.rw("currentDay");
if (cachedDay) {
  initialDay = cachedDay;
}

// ! Check both of the day archives to see if this day is currently filed.
function TimeSheetData() {
  const [CurrentDay, SetCurrentDay] = useState(initialDay);

  const updateKey = (key, value) => {
    const newObj = objectStateUpdate(CurrentDay, key, value);
    lsCache.rw("currentDay", newObj);
    SetCurrentDay(newObj);
  };

  const confirmChanges = () => {
    // finish and send detauks to the unclaimed days pile
    const dayData = lsCache.rw("currentDay");
    if (validateIn.checkIfDayExists(dayData.date)) {
      // ! alert the user that day exists on file
    }
    delete dayData.dateConfirm;
    delete dayData.timeInConfirm;
    delete dayData.timeOutConfirm;
    dayData.uuid = uuid();
    lsCache.arrayAdd("ucDays", dayData);
    lsCache.drop("currentDay");
    SetCurrentDay(day);
  };

  const openExtraTime = () => {
    console.log("Wip");
  };

  const lunchClicked = (e) => {
    updateKey("hadLunch", !CurrentDay.hadLunch);
  };

  let timeInArea = (
    <TimePicker
      updateTime={updateKey}
      keyName="timeIn"
      value={CurrentDay.timeIn}
    />
  );

  if (CurrentDay.timeIn && CurrentDay.timeInConfirm) {
    timeInArea = (
      <TimeDisplay
        timeObj={CurrentDay.timeIn}
        keyName="timeIn"
        updateConfirm={updateKey}
      />
    );
  }

  let DateArea = (
    <DatePicker updateTime={updateKey} keyName="date" value={CurrentDay.date} />
  );

  if (CurrentDay.date && CurrentDay.dateConfirm) {
    DateArea = (
      <DateDisplay
        timeObj={CurrentDay.date}
        keyName="date"
        updateConfirm={updateKey}
      />
    );
  }

  let timeOutArea = (
    <TimePicker
      updateTime={updateKey}
      keyName="timeOut"
      value={CurrentDay.timeOut}
    />
  );

  if (CurrentDay.timeOut && CurrentDay.timeOutConfirm) {
    timeOutArea = (
      <TimeDisplay
        timeObj={CurrentDay.timeOut}
        keyName="timeOut"
        updateConfirm={updateKey}
      />
    );
  }
  let daySummary = "";
  if (
    CurrentDay.timeInConfirm &&
    CurrentDay.timeOutConfirm &&
    CurrentDay.dateConfirm
  ) {
    daySummary = (
      <DaySuimmary
        day={CurrentDay}
        button={
          <Button
            color="success"
            onClick={confirmChanges}
            margin={10}
            width="240px"
          >
            Another Dollar
          </Button>
        }
      />
    );
  }

  return (
    <div>
      <Stack>
        <Box>
          <DataLabel var="subtitle1">Date of work</DataLabel>
          {DateArea}
        </Box>
        <Box>
          <Typography>Time In</Typography>
          {timeInArea}
        </Box>
        <Box>
          <Typography>Time Out</Typography>
          {timeOutArea}
        </Box>
        <Box sx={{ ...myClasses.flexRow, padding: "15px 8px" }}>
          <Typography>Had Lunch</Typography>
          <Checkbox
            defaultChecked
            color="secondary"
            checked={CurrentDay.hadLunch}
            onChange={lunchClicked}
          />
        </Box>
        <Typography
          sx={{ ...myClasses.flexRow, padding: "10px", color: myColors.info }}
          onClick={openExtraTime}
        >
          Anything else?
        </Typography>
        {daySummary}
      </Stack>
    </div>
  );
}

export default TimeSheetData;
