import ls from "local-storage";

const lsCache = {
  rw: (key, val = undefined) => {
    if (val !== undefined) {
      return ls(key, val);
    } else {
      return ls(key);
    }
  },
  drop: (key) => {
    ls.remove(key);
  },
  arrayAdd: (key, val) => {
    let curArr = ls(key);
    if (curArr === null) {
      curArr = [val];
    } else if (Array.isArray(curArr)) {
      curArr.push(val);
      // log = newArr;
    } else {
      console.log("Value stored not array");
      return false;
    }
    ls(key, curArr);
    // console.log(log);
  },
  arrayDel: (key, id, idKey = "uuid") => {
    let curArr = ls(key);
    if (Array.isArray(curArr)) {
      curArr.forEach((item, i) => {
        if (item[idKey] === id) {
          curArr.splice(i);
        }
      });
      // log = newArr;
    } else {
      console.log("Value stored not array");
      return false;
    }
    ls(key, curArr);
  },
};

export default lsCache;
