import {
  Box,
  Button,
  ButtonBase,
  ButtonGroup,
  Divider,
  IconButton,
  TextField,
  Typography,
} from "@mui/material";
import { Check, Clear } from "@mui/icons-material";
import React, { useState } from "react";
import { capitalizeFirstLetter } from "../../functions";

function LabelToInput(props) {
  const [InputOpen, SetInputOpen] = useState(false);
  const [InputValue, SetInputValue] = useState(props.value);

  const openInput = () => {
    SetInputOpen(true);
  };

  const inputChanged = (e) => {
    SetInputValue(e.target.value);
  };

  const keyPressed = (e) => {
    if (e.key === "Enter") {
      submitChange();
    }
  };

  const submitChange = () => {
    // get the value from the input
    props.updateKey(props.keyName, InputValue);
    SetInputOpen(false);
  };

  const clearChange = () => {
    SetInputValue(props.value);
    SetInputOpen(false);
  };

  // ? allows for setting of diiferent input types
  let inputType = "text";
  if (props.type) {
    inputType = props.type;
  }
  // ? the text half of input or text
  let inputOrText = (
    <Box>
      {" "}
      <Typography>{props.label}</Typography>
      <Typography variant="subtitle1" onClick={openInput}>
        {props.value}
      </Typography>
    </Box>
  );

  // ? the input half
  if (InputOpen) {
    inputOrText = (
      <Box sx={{ width: "100%", display: "flex", flexDirection: "row" }}>
        <TextField
          id={`text-field-${props.label}`}
          onChange={inputChanged}
          onKeyPress={keyPressed}
          sx={{
            flexBasis: "2",
            MinWidth: "280px",
            width: "75%",
          }}
          variant="filled"
          type={inputType}
          label={props.label}
          defaultValue={props.value}
        />
        <ButtonGroup variant="filled" sx={{ margin: "10px", maxWidth: "30%" }}>
          <IconButton color="success" onClick={submitChange}>
            <Check />
          </IconButton>
          <IconButton color="error" onClick={clearChange}>
            <Clear />
          </IconButton>
        </ButtonGroup>
      </Box>
    );
  }

  return (
    <div>
      <Box
        sx={{
          padding: "8px",
          marginY: "3px",
          backgroundColor: "#eee",
          "&:hover": {
            opacity: [0.9, 0.8, 0.7],
          },
        }}
      >
        {inputOrText}
      </Box>
      <Divider />
    </div>
  );
}

LabelToInput.defaultProps = {
  type: false,
};

export default LabelToInput;
