import React, { useState } from "react";
import { TextField, IconButton, ButtonGroup, Typography } from "@mui/material";

import { TimePicker as MuiTimePicker } from "@mui/x-date-pickers/TimePicker";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { isValidDate, roundCurrentTime } from "../../functions/time";
import { Check, Clear } from "@mui/icons-material";
import { myClasses } from "../../classes/style";
import { Box } from "@mui/system";

export default function TimePicker(props) {
  let initialDate = roundCurrentTime();

  const [Value, SetValue] = useState(initialDate);

  const handleChange = (newValue) => {
    SetValue(newValue);
  };

  const submitChange = () => {
    props.updateTime([props.keyName, `${props.keyName}Confirm`], [Value, true]);
  };

  const clearChange = () => {
    props.updateTime([`${props.keyName}Confirm`], [true]);
    SetValue(props.initialDate);
  };

  return (
    <Box sx={{ ...myClasses.flexRow , padding : "6px 10px"}}>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <MuiTimePicker
          label={props.label}
          value={Value}
          onChange={handleChange}
          onAccept={submitChange}
          renderInput={(params) => <TextField {...params} />}
        />
        <ButtonGroup variant="filled" sx={{ maxWidth: "30%" }}>
          <IconButton color="success" onClick={submitChange}>
            <Check />
          </IconButton>
          <IconButton color="error" onClick={clearChange}>
            <Clear />
          </IconButton>
        </ButtonGroup>
      </LocalizationProvider>
    </Box>
  );
}

TimePicker.defualtProps = {
  hours: 6,
  minutes: 30,
  label: "Time",
};
