import { ButtonGroup } from "@mui/material";
import React from "react";
import Button from "./Button";

function GreenRedButtons(props) {
  return (
    <ButtonGroup sx={{ margin: "10px 0 0 auto" }} variant="contained">
      <Button
        text={props.greenLabel}
        color="success"
        onClick={props.greenSub}
      />
      <Button text="Clear" color="error" onClick={props.redSub} />
    </ButtonGroup>
  );
}

export default GreenRedButtons;
