import React, { useState } from "react";
import NavItem from "./NavItem";
import Drawer from "@mui/material/Drawer";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";

import { IconButton, Typography } from "@mui/material";
import {
  AccessTime,
  Clear,
  ChevronRight,
  Today,
  Home,
  FeaturedPlayList,
} from "@mui/icons-material";
const navLinks = [
  {
    title: "Home",
    link: "",
    icon: <Home />,
  },
  {
    title: "Time Sheet",
    link: "timesheet",
    icon: <AccessTime />,
  },
  {
    title: "Details",
    link: "details",
    icon: <FeaturedPlayList />,
  },
  {
    title: "History",
    link: "history",
    icon: <FeaturedPlayList />,
  },
];

const actionLinks = [
  {
    title: "About",
    link: "about",
  },
  {
    title: "Close",
    link: "",
    icon: <Clear />,
  },
];

function NavList({ link, NavControls }) {
  const [NavExpand, setNavExpand] = React.useState(false);
  const closeDrawer = () => {
    NavControls.toggleDrawer(false);
    setNavExpand(false);
  };

  const expandNow = () => {
    setNavExpand(true);
  };
  if (NavControls.curState) {
    setTimeout(() => {
      if (NavControls.curState) {
        setNavExpand(true);
      }
    }, 1230);
  }
  let navTitle = (
    <IconButton onClick={expandNow}>
      <ChevronRight />
    </IconButton>
  );
  let navClass = "navList-small";
  if (NavExpand) {
    navTitle = (
      <Typography component="h5" variant="h4" align="center">
        Scraapy
      </Typography>
    );
    navClass = "navList-big";
  }

  return (
    <div>
      <Drawer open={NavControls.curState} onClose={closeDrawer}>
        <div className="navTitleBox">{navTitle}</div>
        <List className={navClass}>
          {navLinks.map((link) => {
            return (
              <NavItem
                expand={NavExpand}
                key={link.title}
                title={link.title}
                icon={link.icon}
                link={link.link}
              />
            );
          })}
          <Divider />
          {actionLinks.map((link) => {
            return (
              <NavItem
                expand={NavExpand}
                key={link.title}
                title={link.title}
                icon={link.icon}
                link={link.link}
              />
            );
          })}
        </List>
      </Drawer>
    </div>
  );
}

export default NavList;
