import dateFormat from "dateformat";

export const myTime = {
  twelveHours: (dateObj) => {
    return dateFormat(dateObj, "hh : MM TT");
  },
};

export const myDate = {
  sameDate: (date1, date2) => {
    console.log(date1);
    try {
      date1 = new Date(date1);
      date2 = new Date(date2);
      if (date1.getDay() === date2.getDay()) {
        return true;
      }
      return false;
    } catch {
      return null;
    }
  },
};
