import { Label } from "@mui/icons-material";
import { Stack } from "@mui/material";
import { Box } from "@mui/system";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { objectMap, objectValueUpdate } from "../../functions";
import { details } from "../../storage/example";
import GreenRedButtons from "../inputs/GreenRedButtons";
import LabelToInput from "../inputs/LabelToInput";

let intialDetails = details;
const cachedDetails = lsCache.rw("details");
if (cachedDetails) {
  intialDetails = cachedDetails;
}

function DetailsArea() {
  const [Details, SetDetails] = useState(intialDetails);

  const updateKey = (key, value) => {
    console.log(key, value);
    SetDetails(objectValueUpdate(Details, key, value));
  };

  const confirmChanges = () => {
    // send detauks to storage
    lsCache.rw("details", Details);
  };

  const resetChanges = () => {
    // set details from storage
    let cached = lsCache.rw("details");
    if (cached) {
      SetDetails(cached);
    }
    SetDetails(details);
  };

  return (
    <div>
      <Stack>
        {objectMap(Details, (key) => {
          return (
            <LabelToInput
              value={Details[key].value}
              label={Details[key].label}
              keyName={key}
              updateKey={updateKey}
            />
          );
        })}
      </Stack>
      <Box>
        <GreenRedButtons
          greenSub={confirmChanges}
          redSub={resetChanges}
          greenLabel="Save"
        />
      </Box>
    </div>
  );
}

export default DetailsArea;
